import Vue from 'vue'
import Router from 'vue-router'

import 'html5-reset/assets/css/reset.css'

// 承载
import activityForTailPKDetail from '../pages/activityForTailPKDetail.vue'
// 主页
import activityForTailPKDetailIndex from '../views/activity-for-tail-pk-detail.vue'

Vue.use(Router)

const router = new Router({
  routes: [{
    path: '/',
    name: 'activityForTailPKDetail',
    component: activityForTailPKDetailIndex
  }]
})

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#activityForTailPKDetail',
  router,
  components: {
    activityForTailPKDetail
  },
  template: '<activityForTailPKDetail/>'
})



